function BaseUrlFetcher() { }

BaseUrlFetcher.getClientBaseUrl = function () {
    let baseURL = window.localStorage.CLIENT_URL || `${process.env.REACT_APP_CLIENT_API}/`
    baseURL = baseURL.replace('/api/', '/')
    return baseURL
}

BaseUrlFetcher.getClaimsBaseUrl = function () {
    let baseURL = window.localStorage.CLAIMS_URL ? window.localStorage.CLAIMS_URL.replace(/\/$/, '')
        : window.localStorage.PARTNER_URL ? window.localStorage.PARTNER_URL.replace(/\/$/, '') 
            : `${process.env.REACT_APP_SERVER_API}`
    return baseURL
}

export default BaseUrlFetcher
